import Vue from 'vue'
import Vuex from 'vuex'
import { getApp, setApp } from '@/utils/app'
import { getUser, setUser } from '@/utils/user'
import {setCache, getCache} from '../utils/common'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    app: getApp(),
  	user: {
      list: [
        {id:1},
        {id:2},
      ]
    },
    layoutActive: 1,
    textValue: '',
    history: [],
    historyStep: 0,
    layer: {},
    stage: {},
    group: {},
    bgImage: {},
    transformer: {},
    activeNode: {},
    transformerImageIndex: 0,
    tabAsideHover: '0',
    uploadList: getCache('__UPLOADLIST__') || [],
    productDetailCaseInfo: getCache('productDetail') || {},
    background: {
      width: 0,
      height: 0,
      x: 0,
      y: 0,
      nwidth: 0,
      nheight: 0,
      nx: 0,
      ny: 0,
    },
    editButtonList: [
      {
        x:1,
        y:1,
        active: 0,
        activeColor: false,
        placement: 'left',
        noBg: false,
      }
    ],
    explodeGrids: [
      {
        x: 0,
        y:0,//x y 为插入图片的时候坐标
        left: '1px',
        top: '1px',//left top 为上层浮动层
        width: '1px',
        height: '1px',
        active: 0,
        index: 0,
        uploadicon: true
      }
    ],
    explodeGridsActive: {},
    cardData: {},
    productDetail: {},
    openFile: false
  },
  mutations: {
  	SET_APP: (state, app) => {
      setApp(app)
			state.app = app
		},
    SET_USER: (state, user) => {
      setUser(user)
      state.user = user
    },
    SET_OPENFILE: (state, openFile) => {
      state.openFile = openFile
    },
    SET_TEXTVALUE: (state, text) => {
      state.textValue = text
    },
    SET_CARDDATA: (state, cardData) => {
      state.cardData = cardData
    },
    SET_LAYERACTIVE: (state, layoutActive) => {
      state.layoutActive = layoutActive
    },
    SET_HISTORY: (state, history) => {
      state.history = history
    },
    SET_HISTORYSTEP: (state, num) => {
      state.historyStep = num
    },
    SET_CHOOSE_COLOR: (state, row) => {
      setUser(row)
      state.choosecolor = row
    },
    SET_BGIMAGE: (state, image) => {
      state.bgImage = image
    },
    SET_LAYER: (state, layer) => {
      state.layer = layer
    },
    SET_GROUP: (state, group) => {
      state.group = group
    },
    SET_ACTIVENODE: (state, node) => {
      state.activeNode = node
    },
    SET_TRANSFORMER: (state, transformer) => {
      state.transformer = transformer
    },
    SET_TRANSFORMERIMAGEINDEX: (state, index) => {
      state.transformerImageIndex = index
    },
    SET_STAGE: (state, stage) => {
      state.stage = stage
    },
    SET_TABASIDEHOVER: (state, tabAsideHover) => {
      state.tabAsideHover = tabAsideHover
    },
    SET_UPLOADLIST: (state, list) => {
      setCache('__UPLOADLIST__', JSON.stringify(list))
      state.uploadList = list
    },
    SET_BACKGROUND: (state, background) => {
      state.background = background
    },
    SET_EDITBUTTONLIST: (state, edit) => {
      state.editButtonList = edit
    },
    SET_EXPLODEGRID: (state, list) => {
      state.explodeGrids = list
    },
    SET_EXPLODEGRIDACTIVE: (state, row) => {
      state.explodeGridsActive = row
    },
    SET_PRODUCTDETAILCASEINFO: (state, info) => {
      setCache('productDetail', info)
      state.productDetailCaseInfo = info
    }
  },
  actions: {
  	setAPP({ commit }, app) {
  	  commit('SET_APP', app)
  	},
    setUSER({ commit }, user) {
      commit('SET_USER', user)
    },
    setLAYER({ commit }, layer) {
      commit('SET_LAYER', layer)
    },
    setSTAGE({ commit }, stage) {
      commit('SET_STAGE', stage)
    },
    setGROUP({ commit }, group) {
      commit('SET_GROUP', group)
    },
    setTRANSFORMER({ commit }, transformer) {
      commit('SET_TRANSFORMER', transformer)
    },
    setTABASIDEHOVER({ commit }, tabAsideHover) {
      commit('SET_TABASIDEHOVER', tabAsideHover)
    },
    setUPLOADLIST({ commit }, list) {
      commit('SET_UPLOADLIST', list)
    },
    setBACKGROUND({ commit }, background) {
      commit('SET_BACKGROUND', background)
    },
    setEDITBUTTONLIST({ commit }, edit) {
      commit('SET_EDITBUTTONLIST', edit)
    },
    setEXPLODEGRID({ commit }, list) {
      commit('SET_EXPLODEGRID', list)
    },
    setEXPLODEGRIDACTIVE({ commit }, row) {
      commit('SET_EXPLODEGRIDACTIVE', row)
    },
    setPRODUCTDETAILCASEINFO({ commit }, info) {
      commit('SET_PRODUCTDETAILCASEINFO', info)
    }
  },
  modules: {
  }
})
