import request from '@/utils/request'


export function login(data) {

  return request({
    url: 'index.php?route=api/logindiy',
    method: 'post',
    data
  })
}
export function register(data) {

  return request({
    url: 'index.php?route=account/register',
    method: 'post',
    data
  })
}



export function addPostCart(data) {
	
  return request({
    url: 'index.php?route=api/cartdiy',
    method: 'post',
    data
  })
}